<template>
  <div class="user-wrap">
    <van-nav-bar
      title="公告"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <div 
      class="notice"
      v-html="item"
      v-for="(item, index) in notice"
      :key="index"
    ></div>
    <van-pagination 
      v-model="currentPage" 
      :page-count="pageTotal"
      mode="simple" 
    />
  </div>
</template>

<script>
export default {
  name: 'notice',
  data () {
    return {
      currentPage: 1,
      pageTotal: 12,
      notice: [
        '42332edsacsdvfdvgdffghfhgf',
        '42332edsacsdvfdvgdffghfhgf',
        '42332edsacsdvfdvgdffghfhgf',
        '42332edsacsdvfdvgdffghfhgf',
        '42332edsacsdvfdvgdffghfhgf'
      ]
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onClickLeft () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="less">
  .notice {
    font-size: 14px;
    padding: 10px;
    margin: 20px;
    word-break: break-all;
    white-space: inherit;
    overflow: hidden;
    min-height: 100px;
    border: 1px solid #07c160;
    border-radius: 8px;
  }
</style>



